import { Component } from '@angular/core';
import { ActionSheetController  ,AlertController,IonicPage,ModalController, NavController, NavParams , InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider} from '../../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';

@IonicPage()
@Component({
  selector: 'page-docente-notas1',
  templateUrl: 'docente-notas1.html',
})
export class DocenteNotas1Page {


  public loading: boolean = true;
  items: any=[];
  items2: any=[];
  generated = '';
titlexx:any;
  public idtipo                 : any;
  public idtipo2                 : any;
  public idnombre                 : any;
  public idnombre2                 : any;
  public idcodigo2                 : any;
  ubica: any;
  fecha: any;
  tipoxxx:any;
  idempresa:any;
  idafiliado:any='na';
  public hasFilter: boolean = false;
  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';
  permisom: any=[];
  potros1: any=[];
  pingreso:boolean=false;
  pconsulta:boolean=false;
  peliminacion:boolean=false;
  pnota:boolean=false;

  pcreacion:boolean=false;
  pactualizacion:boolean=false;
  preporte:boolean=false;
  rolxx:any;
  pfiltro:boolean=false;
  jsonObject2 :any;
  pet:any;
  estadoant:any;
  codigoidioma:any;
  username: string = "";
  idsuscriptor:any;

  constructor( public modalCtrl : ModalController,public actionSheetCtrl: ActionSheetController,public storage: Storage
    ,public alertCtrl: AlertController,public msj: NotificationProvider, public translateService: TranslateService  ,public navCtrl: NavController,
    public navParams: NavParams,     public regService: Auth) {
      this.translateService.get('APPX').subscribe((value) => {
        this.idempresa=value

    })


    this.pet = "Grabación";
    this.estadoant='Grabación',
this.tipoxxx=this.navParams.data.tipo
this.codigoidioma=this.navParams.data.codigoidioma
this.titlexx=this.navParams.data.titlexx
if(this.tipoxxx==undefined ){this.idtipo=2;
  this.titlexx='Listado Oficial'
  this.tipoxxx='2'
}





    this.permisom = String(navParams.get("permiso")).split(",");
    this.potros1 = JSON.parse(JSON.stringify(navParams.get("potros1")))
    this.pingreso=(  this.permisom[0] == 'true');
    this.pconsulta=(  this.permisom[1] == 'true');
    this.peliminacion=(  this.permisom[2] == 'true');
    this.pcreacion=(  this.permisom[3] == 'true');
    this.pactualizacion=(  this.permisom[4] == 'true');
    this.pfiltro=(  this.permisom[5] == 'true');
    this.preporte=(  this.permisom[6] == 'true');

    var someJsonString = navParams.get("potros1");

    // jsonObject will contain a valid JavaScript object
  if(someJsonString!='')
  {

    this.jsonObject2 = JSON.parse(someJsonString);
  }
  }

  public clearSearchg(): void {
    this.gSearchQuery = '';
  }

  busca(op:any) {
    this.estadoant=op
    this.pideregistros( this.rolxx);

      }


  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  });
  }

  randomScalingFactor = function() {
		return Math.ceil(Math.random() * 10.0) * Math.pow(10, Math.ceil(Math.random() * 5));
  };




  public dapermisos2(data,opcion){
    var re='';

    for (var i = 0; i < data.length; i++)
    {
      if(data[i].modulo==opcion)
      {
          re=data[i].permisos.ingreso + ',' + data[i].permisos.consulta+ ',' + data[i].permisos.elimina+ ',' + data[i].permisos.creacion+ ',' + data[i].permisos.actualiza+ ',' + data[i].permisos.filtro+ ',' + data[i].permisos.reporte
        break;

      }

    }

    return re;
  }

  public dapermisos(data,opcion){
    var re:any[];

    for (var i = 0; i < data.length; i++)
    {
      if(data[i].modulo==opcion)
      {
          re=data[i].permisos
        break;

      }

    }


  //DD/MM/YYYY hh:mm A
  return re;

 }


 public dapermisos_visible(opcion){
  var re:boolean=false;

  if(this.jsonObject2)
  {
  for (var i = 0; i < this.jsonObject2.length; i++)
  {
    if(this.jsonObject2[i].modulo==opcion)
    {
        re=!this.jsonObject2[i].permisos.ingreso
      break;

    }

  }
  }


//DD/MM/YYYY hh:mm A
return re;

}

ionViewDidEnter() {


  this.pet = "Grabación";
  this.estadoant='Grabación'
  if(   this.tiposus[6]=='DOCENTE CALUSAC')
  {

    this.rolxx=0
    this.pideregistros( this.rolxx);
  }
  else{
    this.rolxx=1
    this.pideregistros( this.rolxx);

  }
}

  pideregistros(rolxt) {
      let self = this;
      self.loading = true;
      var cad=''
if(rolxt=='0' || rolxt==undefined)
{
  cad=this.idsuscriptor+'/' + this.navParams.data.record.codigo+ '°' +  this.estadoant +'/horarioprofe' +this.navParams.data.tipo+ '/'

}
else
{
  this.rolxx=1
 // cad='1/' +this.estadoant +'/horarioprofe2a/'  // da error cuando  es ubicacion
// cad=this.idsuscriptor+'/' + this.navParams.data.record.codigo+ '°' +  this.estadoant +'/horarioprofe' +this.navParams.data.tipo+ '/'

cad='1/' +this.estadoant +'/horarioprofe2a/'

}


      this.regService.getReg(cad,'asignaubicacions').then((data) => {

      this.items=data;
      this.items2=data;
      self.loading = false;
  }, (err) => {
    this.msj.showError3('Error',err.error);
  });
  }

  tiposus: any=[];

  ionViewDidLoad() {



    this.idsuscriptor=this.navParams.data.idsuscriptor

if( this.rolxx==undefined) {this.rolxx=1}

  this.storage.get('tiposus').then((value) => {
    this.tiposus = value.split(',');







    if(   this.tiposus[6]=='DOCENTE CALUSAC')
    {
      this.rolxx=0
      this.pideregistros( this.rolxx);
    }
    else{
      this.rolxx=1
      this.pideregistros( this.rolxx);

    }

  });




}

  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }

  salonRecord(item : any, no) : void
  {

if(this.tipoxxx=='2')
{
  this.navCtrl.push('DocenteNotas2Page', {titlexx:this.titlexx,rolxx:this.rolxx,tipoxxx:this.tipoxxx, permiso:this.permisom,potros1:this.navParams.get("potros1"),record : item ,idsuscriptor:this.idsuscriptor,ididioma:item.idiomacodigo,idioman:item.idioma});

}
else
{
  this.navCtrl.push('DocenteNotas2uPage', {titlexx:this.titlexx,rolxx:this.rolxx,tipoxxx:this.tipoxxx, permiso:this.permisom,potros1:this.navParams.get("potros1"),record : item ,idsuscriptor:this.idsuscriptor,ididioma:this.codigoidioma,idioman:item.idioma});

}

  }

}



